import { getCodeMIne } from '@/api/index.js';
const wx = require('weixin-js-sdk');

export default {
    name: 'CodeMine',
    data() {
        return {
            host:global.host,
            codedata:Object,
        };
    },
    beforeCreate() {
    },
    mounted() {
        console.log(this.host)
        this.getCodeMIne()
    },
    methods:{
        getCodeMIne(){
            getCodeMIne({
                user_id: this.$store.state.user.userid,
                type:2
            }).then(res => {
                console.log('我的二维码',this.$store.state.user,res)
                if (res.data.code == 1) {
                    this.codedata = res.data.data;
                }
            });
        }  
    },
    components: {
      
    }
};




